  @import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&display=swap');

/* Body and App */

  body, html {
    height: 100%;
    margin: 0;
    display: flex;
    flex-direction: column;
    scroll-behavior: smooth;
  }

  .App {
    text-align: center;
    display: flex;
    transition: all 0.5s ease-in-out;
    flex-direction: column;
    min-height: 100vh;
    height: auto;
    overflow: visible;
  }

  .App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
  }

  /* Keyframes */

  
  @keyframes flicker {
    0%, 18%, 22%, 25%, 53%, 57%, 100% {
      text-shadow: 0 0 10px rgba(255, 255, 255, 0.5), 0 0 20px rgba(255, 255, 255, 0.2), 0 0 30px rgba(255, 255, 255, 0.8);
    }
    20%, 24%, 55% {
      text-shadow: none;
    }
  }

  /* Title Card */

  .title-card {
    position: relative;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    background:transparent;
  }

  .title-card h1 {
    position: relative;
    color: white;
    z-index: 3;
    background-image: linear-gradient(to right, transparent, black, transparent);
    font-size: 6vw; 
    font-family: 'Montserrat', sans-serif;
    padding: 20px;
    text-align: center;
    color: rgba(256, 256, 256, 0.7);
    width: 100%;
    margin: 0; 
    pointer-events: none;
    text-shadow: 0 0 10px rgba(255, 255, 255, 0.5), 0 0 20px rgba(255, 255, 255, 0.2), 0 0 30px rgba(255, 255, 255, 0.8);
    animation: flicker 5s infinite;
  }

  .title-card button {
    position: relative;
    z-index: 2;
    margin-top: 20px; 
    padding: 10px 20px;
    background-color: #fff0;
    color: white;
    border: none;
    background-image: linear-gradient(to right, transparent, black, transparent);
    font-size: 1.5rem;
    font-weight: normal;
    cursor: pointer;
  }

/* Banner */

  .banner {
    margin: 0px;
    text-align: center;
    color:white;
    background: linear-gradient(to top, black, rgb(41, 41, 41), rgb(0, 0, 0));
  }

  .banner h1 {
    margin:0px;
    font-family: 'Montserrat', sans-serif;
  }

/* Footer */

  footer img {
    width: 50px;
    height: 50px;
    margin: 10px;
  }

/* Contact Section */

  .contact {
    height: 100vh; 
    width: 100vw;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: linear-gradient(to bottom, black, transparent, white, transparent); /* Gradient background */
    position: relative;
  }

  .contact::before {
    content: ""; 
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #059dc7;
    opacity: 0.2; 
    z-index: -1; 
  }

  .contact img {
    width: 30px;
    font-family: 'Montserrat', sans-serif;
    height: 30px;
    margin: 0px;
    padding: 0px; 
  }

  .contact p {
    font-size: 2rem;
    font-family: 'Montserrat', sans-serif;
    color: black;
  }

  .contact h1 {
    font-size: 4rem;
    font-family: 'Montserrat', sans-serif;
    color: white;
  }

  /* Language Buttons */

  .lang_btn {
    position: absolute;
    top: 10px;
    right: 10px;
    display: flex;
    gap: 10px;
    scale: .9;
  }

  .lang_btn button {
    padding: 5px 10px;
    cursor: pointer;
  }


/* Slideshow */

  #slideshow {
    width: 50%;
    opacity: 1;
    border-radius: 10%;
  }

  #slideshow:hover{
    cursor: pointer;
    transform: scale(1.05);
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.6);
    transition: transform 0.3s ease, box-shadow 0.3s ease;

  }
  
/* Website fixed background and Video */

.background-layer {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  max-width: 100%;
  max-height: 100%;
  background-image: url('https://via.placeholder.com/150');
  background-size:cover;
  background-position: center center;
  z-index: -1; 
  object-fit: cover; 
  transition: background-image 1s ease-in;
} 

.background-layer video {
  opacity: 0.1;
}

/* Main Content Container */
.maincontent {
  padding: 20px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  font-family: 'Montserrat', sans-serif;
  align-content: center;
  font-size: 1.5rem;
  background: transparent;
  min-height: auto;
  height: auto;
  position: relative;
  overflow: visible;
}

.maincontent::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: url('carbon.jpg') repeat;
  opacity: 0.09;
  z-index: 1;
}

.maincontent > * {
  position: relative;
  z-index: 2;
}

.maincontent button {
  transition: background 0.3s ease-in;
  color: white; 
  border: none; 
  cursor: pointer;
  font-weight: normal;
  width: auto;
  padding: 10px 20px;
  border-radius: 15px;
  font-size: 1rem;
  font-weight: normal;
  background: #333;
  background-position: 0 0, 10px 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.4);
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.8), -2px -2px 4px rgba(255, 255, 255, 0.3);
  
}

.maincontent button:hover {
  background: linear-gradient(to right, #4db800, #078400); 
}

/* Category Container */
.category-container {
  display: flex;
  flex-direction: column;
  margin-bottom: 40px;
  width: 100%;
  padding: 10px;
  background: transparent;
}

.category-container::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 20px;
  background: rgba(186, 186, 186, 0.4); 
  z-index: -1; 
  backdrop-filter: blur(10px); 
}

.category-container h2 {
  margin-bottom: 20px;
  font-size: 2rem;
  color: white;
}

/* Cards Row (Group of Cards) */
.cards-row {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr)); /* Items wrap if they can't fit */
  gap: 20px; /* Space between items */
  width: 100%;
  align-items: center;
  justify-items: center;
}

/* Individual Card */
.card {
  position: relative;
  margin: 0;
  padding: 20px;
  color: white;
  font-size: 1.2rem;
  background: linear-gradient(to bottom, rgb(19, 19, 19), #000000);
  border-radius: 4%;
  box-sizing: border-box;
  flex: 1 1 calc(33.333% - 40px); /* Ensures three cards per row with spacing */
  max-width: calc(30% - 40px);
  min-width: 300px; /* Ensures cards do not get too small */
  height: fit-content;
  overflow: visible;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.4);
}

.card p {
  font-size: 1.2rem;
  color: #333;
  background: linear-gradient(to right, #aaa, #ccc);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  text-shadow: 
    1px 1px 2px rgba(0, 0, 0, 0.8), 
    -1px -1px 2px rgba(255, 255, 255, 0.8), 
    inset 1px 1px 2px rgba(0, 0, 0, 0.8), 
    inset -1px -1px 2px rgba(255, 255, 255, 0.8);
}

.card h3 { 
  font-size: 1.5rem;
  text-shadow: 0 0 10px rgba(255, 255, 255, 0.5), 0 0 20px rgba(255, 255, 255, 0.2), 0 0 30px rgba(255, 255, 255, 0.8);
  animation: flicker 5s infinite;
}

.card:hover {
  transform: scale(1.05);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.6);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

@media(prefers-reduced-motion) {
  
}

/* Responsive Styles */

@media (max-width: 1258px) {
  .card p {
    font-size: 1.1rem;
  }
}

@media (max-width: 951px) {
  .card p {
    font-size: 1rem;
  }
}

